html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
#tap-to-place {
  position: fixed;
  bottom: 20px;
  left: 50%;
  right: 0px;
  border-radius: 15px;
  background: black;
  color: white;
  width: 200px;
  margin-left: -100px;
  text-align: center;
  padding: 15px;
  box-sizing: border-box;
  font-family: sans-serif;
  text-transform: uppercase;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;

  font-family: "Roboto", "Helvetica", sans-serif;

  transition: background-color 0.2s;
  will-change: background-color;
}

.inspired-by {
  display: block;
  position: relative;
  margin-bottom: 15px;
  text-align: center;
  color: #fff;
  font-size: 14px;
}

.wrapper {
  position: relative;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  display: block;
  /* position: absolute; */
  /* top: 0;  */
  /* Adjust top to start from the top of the screen */
  /* left: 0; */
  /* Set left to 0 to fill the entire width */
  margin: 0 auto;
  width: 87%; /* Make it take the full screen width */
  height: 87%;
  background-color: #fff;
  border-radius: 0; /* Remove border-radius to make it rectangular */

  box-shadow: 0 30 50 rgba(0, 0, 0, 0.2);

  /* transform: translateX(-50%); */
  transition: left 0.5s ease-out;
  will-change: left;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}

.card--19 {
  left: 50%;
}

.card--solstice,
.card--huarache {
  left: 150%;
}

.card__header {
  position: relative;
  height: 72%;
  padding: 30px 30px 300px;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  color: #fff;
}

.card__header--19 {
  background-color: #f72648;
  background-image: linear-gradient(#f72648, #fccb3c);

  background: #f72648;
  background: linear-gradient(#f72648, #fccb3c);
}

.card__header--solstice {
  background-color: #3ca3fc;
  background-image: linear-gradient(#3ca3fc, #ffd300);

  background: #3ca3fc;
  background: linear-gradient(#3ca3fc, #ffd300);
}

.card__header--huarache {
  background-color: #26c9f7;
  background-image: linear-gradient(#26c9f7, #dffc3c);

  background: #26c9f7;
  background: linear-gradient(#26c9f7, #dffc3c);
}

.card__watermark {
  overflow: hidden;
  position: absolute;
  bottom: 10px;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__watermark::after {
  content: attr(data-watermark);
  /* position: relative;
  left: -20px; */
  color: rgba(0, 0, 0, 0.3);
  font-size: 240px;
  font-weight: 700;
  text-transform: uppercase;
}

.card__logo {
  width: 50%;
  height: auto;
  /* margin: 0 auto; */
  margin-left: 50%;
  transform: translateX(-50%);
}

.card__price {
  float: right;
  font-size: 16px;
  font-weight: 300;
}

.card__title {
  /* margin: 35px 0 20px;
  font-size: 15px;
  line-height: 1.1em;
  text-transform: uppercase;
  letter-spacing: 1.5px; */
  font-size: 4rem;
  text-align: center;
  line-height: 1.1em;
  letter-spacing: 4px;
}

.card__subtitle {
  display: block;
  font-size: 1rem;
  text-align: center;
  font-weight: 500;
  font-style: italic;
}

.card__body {
  position: relative;
  padding: 40px 30px 20px;
}

.card__image {
  z-index: 1;
  position: absolute;
  top: -290px;
  left: -30px;
  width: 125%;

  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.card__wish-list {
  display: block;
  width: 50%;
  margin: 0 auto 15px;
  padding: 15px;
  border: 2px solid #fff;
  border-radius: 20px;
  text-align: center;
  text-transform: uppercase;
  font-size: 14px;
}

.card__wish-list--19 {
  color: #8850ff;
  border-color: #8850ff;
}

.card__wish-list--solstice {
  color: #ffba00;
  border-color: #ffba00;
}

.card__wish-list--huarache {
  color: #26c9f7;
  border-color: #26c9f7;
}

.card__category {
  display: block;
  font-size: 12px;
  color: #aeaeae;
  text-transform: uppercase;
  text-align: center;
}

.card__will-animate {
  will-change: transform;
}

.cards-placeholder {
  display: block;
  position: relative;
  margin-bottom: 15px;
  text-align: center;
}

.cards-placeholder__item {
  opacity: 0.3;
  display: inline-block;
  margin-right: 10px;
  background-color: #fff;
  width: 30px;
  height: 5px;
  border-radius: 5px;

  transition: opacity 0.2s;
  will-change: opacity;
}

.cards-placeholder__item--active {
  opacity: 1;
}

body {
  background-color: black;
  overflow: hidden;
}
h3 {
  margin-top: 10px;
}
#game-field {
  padding: 0;
  position: relative;
}

#score-wrapper {
  min-width: 120px;
  background-color: white;
  color: black;
  font-size: 23px;
  opacity: 0.7;
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 5px;
  border-radius: 5px;
}

#leader-board-wrapper {
  min-width: 160px;
  background-color: #800000;
  color: white;
  font-size: 18px;
  opacity: 0.7;
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 5px;
}

#leader-board {
  padding: 5px;
}

#sort-wrapper {
  margin-top: -20px;
  font-size: 14px;
}

.sort-option {
  padding: 3px 0;
  width: 33.33%;
  text-align: center;
  float: left;
  border-top: solid 2px black;
}

.sort-option:hover {
  cursor: pointer;
  background-color: #4d0000;
}

.active {
  background-color: #4d0000;
}

#sort-orbs {
  border-left: solid 2px black;
  border-right: solid 2px black;
}

canvas {
  display: block;
  background-color: white;
}
.modal-sm {
  min-width: 500px;
  width: 30%;
}

.modal-content {
  margin-top: 100px;
  background-color: #ffffff;
  border-radius: 10px;
}

.text-input {
  font-size: 20px;
  display: block;
  margin: auto;
  margin-bottom: 20px;
  width: 90%;
  padding: 5px;
  border-radius: 5px;
}

.error-message {
  font-size: 13px;
  color: #e60000;
}

.play-button {
  font-size: 20px;
  display: block;
  margin: 5px auto 5px;
  width: 90%;
  background-color: #00e6e6;
  color: #ffffff;
}

#join-team-btn,
#play-solo-btn,
#create-team-btn,
#create-play-solo-btn,
#add-team-btn {
  width: 48%;
  margin: 5px auto 15px;
  font-size: 18px;
  display: inline-block;
  background-color: #00e6e6;
  color: #ffffff;
}

#create-team-btn,
#add-team-btn,
#join-team-btn {
  background-color: #00e600;
}

.btn-github:hover {
  color: rgba(200, 200, 200, 0.5);
}

.stats-btn {
  width: 48%;
  margin: 0 auto 15px;
  font-size: 18px;
  display: inline-block;
  background-color: #e600e6;
  color: #ffffff;
}

#create-button {
  font-size: 20px;
  display: block;
  margin: 5px auto 5px;
  width: 90%;
  background-color: #00e600;
  color: #ffffff;
}

#playerStats-all-btn,
#playerStats-player-btn {
  font-size: 20px;
  display: block;
  margin: 5px auto 5px;
  width: 90%;
  background-color: #e600e6;
  color: #ffffff;
}

.stats-btn-wrapper,
.play-btn-wrapper {
  width: 90%;
  margin: auto;
}

.btn-wrapper {
  width: 90%;
  margin: 20px auto;
}

.player-stats-btn,
#join-team-btn,
#create-team-btn,
#add-team-btn {
  float: left;
}

.all-stats-btn,
#play-solo-btn,
#create-play-solo-btn {
  float: right;
}

.stats,
.stat-number {
  padding-top: 10px;
  font-size: 20px;
  width: 70%;
  display: inline-block;
}

.stat-number {
  width: 30%;
  font-weight: bold;
}

.modal-footer {
  clear: both;
  text-align: left;
}

.player-stats {
  font-size: 18px;
  font-weight: bold;
}

.teams {
  margin: 10px 0;
}

.join-button {
  color: white;
  background-color: #00e6e6;
  margin-right: 10px;
}

.team {
  font-size: 18px;
}

#game-message-wrapper {
  position: fixed;
  width: 100%;
  bottom: 20px;
}

#game-message {
  display: table;
  font-size: 20px;
  border-radius: 5px;
  padding: 5px 15px;
  color: white;
  background-color: #e600e6;
  margin: auto;
  opacity: 0;
  transition: all 2s;
}
